// Packages
import { CqsItem } from "@rp/common/api-types"
import { Watchlist } from "@rp/common/api-types"
import { WatchlistMenu } from "@rp/react/web/common/composite-components"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { useModal } from "@rp/react/web/common/providers"
import { Typography } from "@rp/react/web/mui/core"
// Styles
import { FilterInputLabelStyled, FilterInputStyled } from "./FilterInput.styles"
// Components
import { QueryCheckbox } from "./QueryCheckbox"

type FilterInputTypes = {
  filterItem: CqsItem
  disableCheckbox?: boolean
  inWatchlist?: boolean
  onChange?: (item: CqsItem) => void
  onClick?: (item: CqsItem) => void
  onAddToWatchlist?: (watchlist: Watchlist) => void
}
const FilterInputMobile = ({
  filterItem,
  disableCheckbox,
  onChange,
  onClick = () => {},
  onAddToWatchlist,
}: FilterInputTypes) => {
  const { key: id, name: title } = filterItem
  const { isMobile } = useDeviceInfo()

  const {
    watchlistModal: { handleOpen },
  } = useModal()
  const handleClick = () => {
    onClick(filterItem)
  }

  const handleChange = () => {
    if (onChange) onChange(filterItem)
  }

  const showWatchlistButton =
    filterItem.queryType === "entity" || filterItem.queryType === "rp_topic"

  return (
    <FilterInputStyled isEntity={!!filterItem.entityType} key={id}>
      <QueryCheckbox
        queryRelationship={filterItem.queryRelationship}
        name={filterItem.name}
        onChange={handleChange}
        placement="left"
        disableCheckbox={disableCheckbox}
      />
      <FilterInputLabelStyled onClick={handleClick}>
        <EllipsisTooltip tooltipText={title}>
          <Typography display="inline" variant="body1">
            {title}
          </Typography>
        </EllipsisTooltip>
      </FilterInputLabelStyled>
      {showWatchlistButton && !isMobile ? (
        <WatchlistMenu
          filter={[filterItem]}
          onAddToWatchlist={onAddToWatchlist}
          onHandleOpenModal={handleOpen}
        />
      ) : null}
    </FilterInputStyled>
  )
}
export { FilterInputMobile }
